<template>
  <div class="report">
    <Loader v-if="report.loading" />
    <div class="inputs">
      <div
        class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
      >
        <div class="md-layout md-alignment-top-space-between">
          <md-datepicker
            name="start"
            v-model="report.range.start"
            class="md-layout-item md-size-45"
            :md-disabled-dates="report.loading ? () => true : disabledDates"
          />
          <md-datepicker
            name="end"
            v-model="report.range.end"
            class="md-layout-item md-size-45"
            :md-disabled-dates="report.loading ? () => true : disabledDates"
          />
        </div>
      </div>
      <md-button
        class="md-layout-item md-primary"
        :disabled="report.loading"
        @click="loadJournal"
        >Загрузить</md-button
      >
    </div>
    <div v-html="report.result" class="result"></div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader.vue";

export default {
  data: () => ({
    range: {
      get start() {
        return this._start || null;
      },
      set start(val) {
        return (this._start = new Date(val));
      },
      get end() {
        return this._end || null;
      },
      set end(val) {
        return (this._end = new Date(val));
      }
    },
    report: {
      range: {
        start: null,
        end: null
      },
      result: "",
      loading: false
    },
    disabledDates(date) {
      let edges =
        (this.$options.parent.$parent.$parent.$attrs.name == "end" &&
          date <
            this.$options.parent.$parent.$parent.$parent.report.range.start) ||
        (this.$options.parent.$parent.$parent.$attrs.name == "start" &&
          date > this.$options.parent.$parent.$parent.$parent.report.range.end);
      return (
        edges ||
        date < this.$options.parent.$parent.$parent.$parent.range.start ||
        date > this.$options.parent.$parent.$parent.$parent.range.end
      );
    }
  }),
  methods: {
    ...mapActions(["GetRange", "GetJournal"]),
    loadJournal() {
      this.report.loading = true;
      this.GetJournal(this.report.range)
        .then(html => (this.report.result = html))
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.report.loading = false));
    }
  },
  watch: {
    "report.range": {
      handler(val) {
        localStorage["vi-sgo-report"] = JSON.stringify(val);
      },
      deep: true
    }
  },
  created() {
    this.report.loading = true;
    this.GetRange()
      .then(range => {
        this.range.start = range.start;
        this.range.end = range.end;

        const localRange = JSON.parse(localStorage["vi-sgo-report"] || "{}");
        this.report.range.start = new Date(
          localRange?.start || this.range.start
        );
        this.report.range.end = new Date(localRange?.end || this.range.end);
        return void 0;
      })
      .catch(err => this.$root.$emit("catch-error", err))
      .finally(() => (this.report.loading = false));
  },
  components: {
    Loader
  }
};
</script>

<style>
div.report div.inputs {
  margin: 0 0.8rem;
}
div.report .md-field.md-layout-item input {
  width: 100%;
}
div.report div.result {
  margin: 0 0.6rem;
  overflow-x: auto;
  width: calc(100% - 2 * 0.6rem);
}
div.report div.result h5.report-title-school {
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}
div.report div.result h2[align="center"] {
  font-size: 18px;
  font-weight: 450;
  text-align: center;
}
div.report div.result div#photowrap img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  object-position: top;
}
div.report div.result span.select {
  font-size: 14px;
  font-weight: 300;
}
div.report div.result span.select b {
  font-weight: 450;
}
div.report div.result table.table-print {
  box-sizing: border-box;
  border-spacing: inherit;
}
div.report div.result table.table-print th,
div.report div.result table.table-print td {
  padding: 2px;
  border: 0.5px solid rgba(0, 0, 0, 0.12);
}
div.report div.result table.table-print th {
  font-weight: 500;
  background-color: #0000000a;
}
div.report div.result table.table-print td:not(.cell-num):not(.cell-text) {
  min-width: 30px;
  max-width: 30px;
  text-align: center;
}
div.report div.result table.table-print td.cell-num,
div.report div.result table.table-print td.cell-text {
  padding: 2px 8px;
  white-space: nowrap;
}
div.report div.result table.table-print td.cell-num {
  text-align: center;
}
div.report div.result div.legend p {
  margin: 2px 0;
  font-size: 12px;
  font-weight: 300;
}
div.report div.result span.smalltext {
  opacity: 0.8;
  font-size: 10px;
  line-height: 10px;
}
</style>
