import Vue from "vue";

class Guid {
  constructor() {
    this._genHistory = [];
  }

  genID() {
    let id;
    do {
      id = "xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx".replace(/[xy]/g, c => {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    } while (this._genHistory.includes(id));
    this._genHistory.push(id);
    return id;
  }

  checkID(id) {
    return this._genHistory.includes(id);
  }
}
const messageGuid = new Guid();
class SocketRequest {
  constructor(obj) {
    this._id = messageGuid.genID();
    this._send = false;
    this._message = obj;
    this._timestamp = new Date().toJSON();
    this._callbackDone = obj?.callback?.done;
    this._callbackError = obj?.callback?.error;
  }

  get id() {
    return this._id;
  }

  get send() {
    return this._send;
  }
  set send(val) {
    return (this._send = val);
  }

  get message() {
    return JSON.stringify({
      mid: this._id,
      method: this._message.method,
      data: this._message.data,
      timestamp: this._timestamp
    });
  }

  get rawMessage() {
    return {
      mid: this._id,
      method: this._message.method,
      data: this._message.data,
      timestamp: this._timestamp
    };
  }

  get time() {
    return new Date(this._timestamp);
  }

  callback(msg) {
    const error = "error" in msg;
    this[error ? "_callbackError" : "_callbackDone"]?.call(
      this,
      msg[error ? "error" : "data"]
    );
  }
}

export default {
  state: {
    isConnected: false,
    messages: []
  },
  actions: {
    // Cache: off
    CheckHost({ dispatch }, host) {
      return dispatch("SendMessage", {
        method: "CheckHost",
        data: { host }
      }).then(({ canWork }) => canWork);
    },
    AuthForm({ dispatch }, host) {
      return dispatch("SendMessage", {
        method: "AuthForm",
        data: { host }
      }).then(({ selectors }) => selectors);
    },
    SelectItems({ dispatch }, data) {
      return dispatch("SendMessage", {
        method: "SelectedData",
        data
      }).then(({ items }) => items);
    },
    AddUser({ dispatch }, data) {
      return dispatch("SendMessage", {
        method: "AddUser",
        data
      }).then(({ id }) => id);
    },
    AddUserViaLink({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "AddUserViaLink",
        data: { id }
      }).then(({ id }) => id);
    },
    CanShare({ dispatch }) {
      return dispatch("SendMessage", {
        method: "CanShare"
      }).then(({ can }) => can);
    },
    ChangeUser({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "ChangeUser",
        data: { id }
      }).then(({ changed }) => changed);
    },
    GetAuthLink({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "GetAuthLink",
        data: { id }
      }).then(({ link }) => link);
    },
    LogIn({ dispatch }) {
      return dispatch("SendMessage", {
        method: "LogIn"
      }).then(({ auth }) => auth);
    },
    LogOut({ dispatch }) {
      return dispatch("SendMessage", {
        method: "LogOut"
      }).then(({ auth }) => auth);
    },
    Exit({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "Exit",
        data: { id }
      }).then(({ leave }) => leave);
    },
    Unlimited({ dispatch }, id) {
      return dispatch("SendMessage", {
        method: "Unlimited",
        data: { id }
      }).then(({ limited }) => limited);
    },
    CommonUsers({ dispatch }) {
      return dispatch("SendMessage", {
        method: "CommonUsers"
      }).then(({ users }) => users);
    },
    CheckAuth({ dispatch }) {
      return dispatch("SendMessage", {
        method: "CheckAuth"
      }).then(({ need }) => need);
    },
    TimeOutAuth({ dispatch }) {
      return dispatch("SendMessage", {
        method: "TimeOutAuth"
      }).then(({ time }) => time);
    },
    // Cache: on
    GetTypes({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "Types",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetTypes"
          }).then(value => dispatch("saveCache", { name: "Types", value }))
      }).then(({ types }) => types);
    },
    GetRange({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "Range",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetRange"
          }).then(data => dispatch("saveCache", { name: "Range", value: data }))
      }).then(({ range }) => range);
    },
    GetSubjectsID({ dispatch }) {
      return dispatch("getCache", {
        time: 864e5,
        name: "Subjects",
        next: () =>
          dispatch("AuthBefore", {
            method: "GetSubjectsID"
          }).then(value => dispatch("saveCache", { name: "Subjects", value }))
      }).then(({ subjects }) => subjects);
    },
    GetDiary({ dispatch }, data) {
      const name = `Diary${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetDiary",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      });
    },
    GetMark({ dispatch }, id) {
      const name = `Mark${id}`;
      return dispatch("getCache", {
        name,
        time: 864e5,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetMark",
            data: { id }
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ assignment }) => assignment);
    },
    GetSubject({ dispatch }, data) {
      const name = `Subject${data.id}${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetSubject",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      }).then(({ subject }) => subject);
    },
    GetJournal({ dispatch }, data) {
      const name = `Journal${data.start}${data.end}`;
      return dispatch("getCache", {
        name,
        time: 12e4,
        next: () =>
          dispatch("AuthBefore", {
            method: "GetJournal",
            data
          }).then(value => dispatch("saveCache", { name, value }))
      });
    },
    GetTotalMarks({ dispatch }) {
      return dispatch("TimeOutAuth")
        .then(time => (!time ? dispatch("LogIn") : void 0))
        .then(() =>
          dispatch("SendMessage", {
            method: "GetTotalMarks"
          })
        );
    },
    // System: true
    AuthBefore({ dispatch }, obj) {
      return dispatch("LogIn").then(() => dispatch("SendMessage", obj));
    },
    SendMessage({ state, commit }, obj) {
      return new Promise((done, error) => {
        obj.callback = {
          done,
          error
        };

        const req = new SocketRequest(obj);
        req.send = state.isConnected;
        if (state.isConnected) Vue.prototype.$socket.send(req.message);
        commit("addMessage", req);
      });
    }
  },
  mutations: {
    addMessage(state, req) {
      state.messages.push(req);
    },
    SOCKET_ONOPEN(state) {
      state.isConnected = true;
      for (let message of state.messages) {
        if (message.send) continue;
        if (!state.isConnected) return;
        message.send = true;
        Vue.prototype.$socket.send(message.message);
      }
    },
    SOCKET_ONCLOSE(state) {
      state.isConnected = false;
    },
    SOCKET_ONERROR(state, event) {
      console.group("SOCKET_ONERROR");
      console.info("State: ", state);
      console.error("Event: ", event);
      console.groupEnd();
    },
    SOCKET_ONMESSAGE(state, message) {
      try {
        message = JSON.parse(message.data);
      } catch (e) {
        console.error("The message couldn't be processed.", e);
        return false;
      }
      if (!message.mid) return;
      const req = state.messages.find(m => message.mid === m.id);
      req.callback(message);
    },
    SOCKET_RECONNECT(state, count) {
      console.group("SOCKET_RECONNECT");
      console.info("Count: ", count);
      console.info("Messages: ", state.messages);
      console.info("Listeners: ", state.listeners);
      console.groupEnd();
    }
  }
};
