export default {
  state: {
    storage: {
      uid: localStorage.uid,
      darkMode: false,
      subjectDates: {},
      diaryStripes: false,
      connectedUsers: [localStorage.uid],
      diaryWaitTime: 3000,
      noIncludeSunday: false,
      ...JSON.parse(localStorage["vi-sgo-settings"] || "{}")
    }
  },
  actions: {
    saveSettings({ state }) {
      localStorage["vi-sgo-settings"] = JSON.stringify(state.storage);
    }
  }
};
